import { Injectable, ViewContainerRef } from '@angular/core';
import { CdkDrag, CdkDragDrop, CdkDropList } from '@angular/cdk/drag-drop';
import { BehaviorSubject } from 'rxjs';
import { BaseDynamicComponent } from '../components/base-dynamic-component';
import { BaseUIComponentConstructor, Categories, ComponentDescriptor, ComponentEvent, UIModel } from '../models';
import { DraggableComponent } from './components/draggable-component';
import { NGXDynamicComponent } from '../components/ngx-dynamic-component';
import { CoreService } from '../services/core.service';

export type UIComponent = BaseDynamicComponent | NGXDynamicComponent;

@Injectable()
export class DesignerRendererService {
  selectedContainer$ = new BehaviorSubject<BaseDynamicComponent>(null);
  selectedComponent$ = new BehaviorSubject<BaseDynamicComponent>(null);
  selectedElement$ = new BehaviorSubject<BaseDynamicComponent>(null);
  renderChildren(parentComponent: BaseDynamicComponent): BaseDynamicComponent[] {
    if (!parentComponent.containerRef || !parentComponent.uiModel) {
      return;
    }
    parentComponent.containerRef.clear();
    return parentComponent.uiModel?.children?.map(m => this.createComponent(parentComponent, m));
  }

  createComponent(
    parentComponent: UIComponent,
    uiModel: UIModel,
    containerRef = parentComponent.containerRef,
    dataModel = parentComponent.dataModel,
    index?: number): BaseDynamicComponent  {
    try {
      const descriptor = CoreService.getComponentDescriptor(uiModel.type);
      const componentClass = descriptor.component;
      let component: BaseDynamicComponent;
      if (descriptor.category === Categories.HTML) {
        component = this.creatHTMLComponent(descriptor, parentComponent, uiModel, containerRef, dataModel, index);
      } else if (componentClass.prototype instanceof BaseDynamicComponent) {
        const componentRef = containerRef.createComponent(
          componentClass as BaseUIComponentConstructor,
          { index }
        );
        component = componentRef.instance as BaseDynamicComponent;
        component.dataModel = dataModel;
        component.uiModel = uiModel;
        component.create(componentRef.location.nativeElement);
        this.setDragData(component);

        if (descriptor.category === Categories.Containers) {
          this.subscribeOnContainers(component);
        }
      }
      if (component) {
        component.parent = parentComponent as BaseDynamicComponent;
        component.element.classList.add('dc-element');
        uiModel.getComponent = () => component;
        component.changedDataModel.subscribe((evt) => {
          parentComponent.changedDataModel.emit(evt);
        });
        component.eventHandlers.subscribe((evt: ComponentEvent) => {
          if (parentComponent instanceof NGXDynamicComponent) {
            evt.rootUIModel = parentComponent.uiModel;
          }
          parentComponent.eventHandlers.emit(evt);
        });
        return component;
      } else {
        throw new Error('Not able to create component');
      }
    } catch (error) {
      console.error(error);
      throw new Error(`Failed to create component ${uiModel.type}`);
    }
  }

  private setDragData(component: BaseDynamicComponent): void {
    try {
      const drag = component.injector.get(CdkDrag);
      drag.disabled = true;
      console.log('setDragData', component.uiModel.type, drag);
      if (drag) {
        drag.data = component.uiModel;
        drag.element.nativeElement.onclick = (evt) => {
          evt.stopPropagation();
          this.deselectComponent(this.selectedComponent$.value);
          this.selectedComponent$.next(component);
          if (this.selectedContainer$.value !== component.parent && component.parent) {
            this.selectContainer(component.parent);
          }
          drag.element.nativeElement.classList.add('selected-component');
        };
      } else {
        console.log('no drag!!!', component);
      }
    } catch (e) {
      console.log(component.uiModel, e.message);
    }
  }

  private deselectComponent(component: BaseDynamicComponent): void {
    if (!component) {
      return;
    }

    const descriptor = CoreService.getComponentDescriptor(component.uiModel.type);
    if (descriptor.category === Categories.HTML) {
      component.element.classList.remove('selected-component');
    } else {
      component.injector.get(CdkDrag).element.nativeElement.classList.remove('selected-component');
    }
  }

  private subscribeOnContainers(component: BaseDynamicComponent): void {
    try {
      const dropList = component.injector.get(CdkDropList);
      console.log('subscribeOnContainers', dropList.element.nativeElement);
      dropList.element.nativeElement.onclick = (evt) => {
        evt.stopPropagation();
        this.selectContainer(component);
      };
      dropList.dropped.subscribe((event: CdkDragDrop<Array<any>>) => {
        console.log('dropped-evt000', event.container.element.nativeElement, event.item.element.nativeElement);

        if (event.previousContainer === event.container && event.previousIndex === event.currentIndex) {
          return;
        }

        const container = this.selectedContainer$.value; // event.container.data as unknown as UIComponent;
        const item = event.item.data;
        // console.log('www', container, component.parent, event.container);
        try {
          const previousContainer = event.previousContainer.data as unknown as UIComponent;
          // console.log('previousContainer', event.previousIndex, item.type, event);
          // console.log('el', (previousContainer.containerRef.get(event.previousIndex) as any).rootNodes);
          if (event.previousContainer.id !== 'ngx-components-list') {
            // previousContainer.containerRef.remove(event.previousIndex);
            this.selectedContainer$.value?.containerRef.remove(event.previousIndex);
            this.selectedContainer$.value?.children.splice(event.previousIndex, 1);
          }
          const newComponent = this.createComponent(
            container,
            item as UIModel,
            container.containerRef,
            container.dataModel,
            event.currentIndex
          );
          newComponent.injector.get(CdkDrag).disabled = false;
          this.selectedContainer$.value?.children.splice(event.currentIndex, 0, newComponent);
          // event.previousContainer.removeItem(event.item);
          // event.previousContainer.element.nativeElement.removeChild();
          // event.item.ngOnDestroy();
        } catch (e) {
          console.log('failed to remove child', e);
        }
      });
    } catch (e) {
      console.log(component.uiModel.type, e.message);
    }
  }

  deleteComponent(component: BaseDynamicComponent): void {
    const index = component.parent.children.indexOf(component);
    if (index > -1) {
      component.parent.containerRef.remove(index);
    }
  }

  private selectContainer(component: BaseDynamicComponent): void {
    const dropList = component.injector.get(CdkDropList);
    const previousContainer = this.selectedContainer$.value;
    if (previousContainer === component) {
      return;
    }
    if (previousContainer) {
      previousContainer.children.filter(child => child.injector).forEach((child) => {
        child.injector.get(CdkDrag).disabled = true;
      });
      previousContainer.injector.get(CdkDropList).element.nativeElement.classList.remove('selected-container');
    }
    this.selectedContainer$.next(component);
    dropList.element.nativeElement.classList.add('selected-container');
    component.children.filter(child => child.injector).forEach((child) => {
      child.injector.get(CdkDrag).disabled = false;
    });
  }

  private creatHTMLComponent(
    descriptor: ComponentDescriptor,
    parentComponent: UIComponent,
    uiModel: UIModel,
    containerRef: ViewContainerRef,
    dataModel: any,
    index: number): BaseDynamicComponent {
    const baseHtml = descriptor.component as any;
    const component = new baseHtml(parentComponent.injector);
    component.dataModel = dataModel;
    component.uiModel = uiModel;

    const draggableWrapper = containerRef.createComponent(DraggableComponent, {
      index
    });
    component.create(draggableWrapper.instance.container.element.nativeElement);

    try {
      draggableWrapper.injector.get(CdkDrag).data = component.uiModel;
      // console.log('rrr', component.element);
      component.element.onclick = () => {
        this.deselectComponent(this.selectedElement$.value);
        this.selectedComponent$.next(component);
        component.element.classList.add('selected-component');
      };
    } catch (e) {
      console.error(e);
    }
    return component;
  }
}
